import {action, actions} from '../actions'
import {date} from '../helpers'
import {getPageInsights} from './insights'

export const setGlobalPeriod = (pageId, from, to = date()) => dispatch => {
  from = (typeof from === 'number')
    ? date(from)
    : from.getTime()
  if (to instanceof Date) {
    to = to.getTime()
  }

  dispatch(action(actions.setGlobalPeriod, {from, to}))
  dispatch(getPageInsights(pageId))
}
